.main_background{
    height: 100%;
    width: 100%;
    background: linear-gradient(118.42deg, #003445 1.66%, #000B0F 99.12%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.main_background img{
    max-width: 200px;
    width: 100%;
    height: 200px;
}

.second_para{
    color: #EEEEEE;
    font-weight: 500;
    font-size: 30px;
    line-height: 38px;
    letter-spacing: 0.02em;
    text-decoration-line: underline;
}


@media (max-width: 576px) {
    .main_background img{
        max-width: 576px;
        width: 100%;
        height: 100px;
    }
    .second_para{
        font-size: 15px;
        line-height: 18px;
    }
}

@media (max-width: 786px) and (min-width: 576px) {
    .main_background img{
        max-width: 786px;
        width: 150%;
        height: 100px;
    }
    .second_para{
        font-size: 20px;
        line-height: 19px;
    }
}